.layout {
    background: transparent;
}

.layout div {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 28px;
    color: #fff;
    text-align: center;
    background: transparent;
}

.layout div img {
    width: 100%;
    height: 100%;
    object-fit: contain;
    max-width: 100%;
    max-height: 100%;
    margin: 0 auto;
}

.react-grid-item:hover .react-resizable-handle {
    display: block !important;
}

.react-grid-item .react-resizable-handle {
    display: none;
}

.mobile-layout .layout div {
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
}

.mobile-layout .layout div img {
    max-width: 100%;
    max-height: 100%;
    width: auto;
    height: auto;
}

.mobile-layout .layout div .MuiTypography-root {
    margin: 20px;
}